import pickergroup from "../../components/pickergroup.vue";
import upimgBox from "../../components/upimgbox.vue";
var app = require("../../common/common.js");
var id;
export default {
	components:{
		pickergroup,
		upimgBox
	},
	data:function(){
		return {
			pageLoad:false, 
			pageHide:false,
			pageData:{},
		}
		
	},
	onLoad: function (option) {
		id = option.id;
		this.getPage();
	},
	onReady: function () {
		uni.setNavigationBarTitle({
			title: "发布微贴",
		})
	},
	methods: {
		
		getPage: function () {
			var that = this;
			uni.request({
				url: app.apiHost + "/module.php?fromapp=wxapp&m=b2b_blog&ajax=1&a=add&authcode="+app.getAuthCode(),
				success: function (res) {
					if(res.data.error==1000){
						that.app.goLogin();
						return false;
					}
					that.pageLoad = true;
					that.pageData = res.data.data;

				}
			})
		},
		
		formSubmit:function(e){
			uni.request({
				url:app.apiHost+"/module.php?fromapp=wxapp&m=b2b_blog&a=save&ajax=1&authcode="+app.getAuthCode(),
				data:e.detail.value,
				method:"POST",
				header:{
					"content-type":"application/x-www-form-urlencoded"
				},
				success:function(res){
					uni.showToast({
						title: res.data.message,
						duration: 2000
					});
					if(!res.error){
						uni.navigateBack({
							delta: 2
						});
					}
				}
			})
		}
	},
}
